function Grouping(v, c) {
  return v.filter((o) => !c.find((Y) => o.operation === Y.operation));
}

function getSubsets(array, sum) {
  const result = [];
  function fork(i = 0, s = 0, t = []) {
    if (s === sum) {
      result.push(t);
      return;
    }
    if (i === array.length || result.length > 0) {
      return;
    }
    if (s + array[i].countops <= sum) { // shout circuit for positive numbers only
      fork(i + 1, s + array[i].countops, t.concat(array[i]));
    }
    fork(i + 1, s, t);
  }

  fork();
  return result;
}

function Getgroups(v, d = []) {
  let c = [...d];
  const x = getSubsets(v, 1)?.[0];
  if (!x) {
    return c;
  }
  c = [...c, ...x];
  Getgroups(Grouping(v, c), c);
  return c;
}

