const MyField = ({ initializeStyleOptions, ...props }) => {
  const {
    values: { category },
    setFieldValue
  } = useFormikContext();

  const [field, meta] = useField(props);

  React.useEffect(() => {
    // set the value of textC, based on textA and textB
    if (category) {
      setFieldValue(props.name, null);
      initializeStyleOptions(category);
    }
  }, [category, setFieldValue]);

  return (
    <>
      <DropdownCombobox
        {...props}
        {...field}
        onChange={(e, v) => setFieldValue(props.name, v)}
      />
      {!!meta.touched && !!meta.error && <div>{meta.error}</div>}
    </>
  );
};

