const useCategoryDropdown = (id) => {
  const { get, response } = useRequest();
  const [options, setOptions] = React.useState([]);
  const [defaultValue, setDefaultValue] = React.useState(null);

  async function initializeData() {
    let items = await get('/api/Categories');
    if (response.ok) {
      items = items.map((o) => {
        const res = {
          label: o.Category_Name,
          key: o.Id
        };
        if (id === o.Id) {
          setDefaultValue(res);
        }
        return res;
      });
      setOptions(items);
    }
  }

  React.useEffect(() => {
    initializeData();
  }, []);

  return { options, defaultValue };
};

const useStyleDropdown = (id) => {
  const { get, response } = useRequest();
  const [options, setOptions] = React.useState([]);
  const [defaultValue, setDefaultValue] = React.useState(null);

  async function initializeData(c) {
    let items = await get(`/api/styles?categoryId=${c.key}`);
    if (response.ok) {
      items = items.map((o) => {
        const res = {
          label: o.Style_Name,
          key: o.Id
        };
        if (id === o.Id) {
          setDefaultValue(res);
        }
        return res;
      });
      setOptions(items);
    }
  }

  // React.useEffect(() => {
  //   initializeData();
  // }, []);

  return { options, defaultValue, initializeData };
};

const useStyleMappingsDropdown = (style, defaults) => {
  const { get, response } = useRequest();
  const [options, setOptions] = React.useState([]);
  const [defaultValue, setDefaultValue] = React.useState(null);
  async function initializeData(c, key) {
    let items = await get(`/api/StyleMappings/${c.key}?key=${key}`);
    const dvs = [];
    if (response.ok) {
      // setData(items);
      items = items.map((o) => {
        const res = {
          label: o.Operation_Name,
          key: o.Id,
          ...o
        };
        if ((defaults || []).includes(o.Id)) {
          dvs.push(res);
        }
        return res;
      });
      setOptions(items);
      setDefaultValue(defaults ? dvs : null);
    }
  }
  React.useEffect(() => {
    if (style) {
      initializeData(style);
    }
  }, [style]);
  return {
    options, defaultValue, initializeData
  };
};
