const Details = ({ onApply, record }) => (
  <Paper elevation={3} sx={{ p: 2, mb: 2 }}>
    <Formik
      initialValues={record || initialValues}
      validationSchema={Yup.object().shape({
        category: Yup.object().required('category is required'),
        style: Yup.object().required('style is required'),
        garment: Yup.string().required(),
        target: Yup.number().required(),
        absenteeism: Yup.number().required(),
        workTimeInMin: Yup.number().required()
      })}
      onSubmit={(values, { setSubmitting }) => {
        onApply?.(values);
        setSubmitting(false);
        // console.log(values);
      }}
    >
      {(props) => (<FormComp {...props} record={record} />)}
    </Formik>
  </Paper>
);

