const CustomerListResults = () => {
  const columns = React.useMemo(
    () => [
      {
        Header: 'Machine',
        accessor: 'Machine_Name',
        id: 'Machine_Name',
        columnName: 'Machine',
        type: 'text',
        editable: true,
        displayKey: 'Machine_Name',
        // Cell: ({
        //   value,
        // }) => {
        //   console.log(value);
        //   return value.Machine_Name;
        // }
      },
      {
        Header: 'Operation',
        accessor: 'Operation_Name',
        id: 'Operation_Name',
        columnName: 'Operation',
        type: 'text',
        editable: true,
        displayKey: 'Operation_Name',
        // Cell: ({
        //   value
        // }) => value.Operation_Name
      },
      {
        Header: 'SAMValue',
        accessor: 'SAM_Value',
        id: 'SAM_Value',
        columnName: 'SAMValue',
        type: 'text',
        editable: true
      }
    ],
    []
  );

  const [data, setData] = React.useState(null);
  const [refresh, setRefresh] = React.useState(0);
  const [skipPageReset] = React.useState(false);

  const {
    get, post, response, loading, del, put
  } = useRequest();

  const [category, setCategory] = React.useState(null);
  const [categoryOptions, setCategoryOptions] = React.useState([]);

  const [style, setStyle] = React.useState(null);
  const [styleOptions, setStyleOptions] = React.useState([]);

  // const [operationsOptions, setOperations] = React.useState([]);
  // const [machineOptions, setMachines] = React.useState([]);

  async function initializeData(c, key) {
    const initialTodos = await get(`/api/StyleMappings/${c.key}?key=${key}`);
    if (response.ok) {
      setData(initialTodos || []);
    }
  }
  async function addData(body, type) {
    const finalBody = {};
    Object.entries(body).forEach(([key, value]) => {
      if (key === 'Machine') {
        finalBody.MachineId = value.key;
      } else if (key === 'Operation') {
        finalBody.OperationId = value.key;
      } else {
        finalBody[key] = value;
      }
    });
    if (type === 'Add') {
      await post(`/api/StyleMappings?styleId=${style.key}`, finalBody);
    } else if (type === 'Edit') {
      await put(`/api/StyleMappings?styleId=${style.key}`, finalBody);
    }
    if (response.ok) {
      setRefresh((o) => o + 1);
    }
  }
  async function deleteRows() {
    await del(`/api/styles?categoryId=${category}`);
    if (response.ok) {
      setRefresh((o) => o + 1);
    }
  }

  React.useEffect(() => {
    if (style) {
      initializeData(style, refresh);
    }
  }, [refresh, style]);

  const handleChangeCategory = React.useCallback((event, o) => {
    event?.stopPropogation?.();
    setCategory(o);
  }, []);

  async function initializeCategories() {
    let items = await get('/api/Categories');
    items = items.map((o) => ({
      label: o.Category_Name,
      key: o.Id
    }));
    if (response.ok) {
      setCategoryOptions(items);
    }
  }
  React.useEffect(() => {
    initializeCategories();
  }, []);

  const handleChangeStyle = React.useCallback((event, o) => {
    event?.stopPropogation?.();
    setStyle(o);
  }, []);

  async function initializeStyleOptions(c) {
    let items = await get(`/api/styles?categoryId=${c.key}`);
    items = items.map((o) => ({
      label: o.Style_Name,
      key: o.Id
    }));
    if (response.ok) {
      setStyleOptions(items);
    }
  }

  React.useEffect(() => {
    if (category) {
      initializeStyleOptions(category);
    }
  }, [category]);

  // async function initializeOperations() {
  //   let items = await get('/api/Operations');
  //   items = items.map((o) => ({
  //     label: o.Operation_Name,
  //     key: o.Id
  //   }));
  //   if (response.ok) {
  //     setOperations(items);
  //   }
  // }

  // React.useEffect(() => {
  //   initializeOperations();
  // }, []);

  // async function initializeMachines() {
  //   let items = await get('/api/Machines');
  //   items = items.map((o) => ({
  //     label: o.Machine_Name,
  //     key: o.Id
  //   }));
  //   if (response.ok) {
  //     setMachines(items);
  //   }
  // }

  // React.useEffect(() => {
  //   initializeMachines();
  // }, []);

  // const tableOptions = React.useMemo(
  //   () => ({ Operation: operationsOptions, Machine: machineOptions }),
  //   [operationsOptions, machineOptions]
  // );

  return (
    <div>
      <CssBaseline />
      <Backdrop open={loading} />
      <Grid
        container
        justify="center"
        spacing={2}
        sx={{
          marginBottom: 2
        }}
      >
        <Grid key={1} item>
          <DropdownCombobox
            onChange={handleChangeCategory}
            size="small"
            value={category?.label}
            options={categoryOptions}
            label="Category"
          />
        </Grid>
        <Grid key={2} item>
          <DropdownCombobox
            onChange={handleChangeStyle}
            size="small"
            value={style?.label}
            options={styleOptions}
            label="Style"
          />
        </Grid>
      </Grid>

      <EnhancedTable
        columns={columns}
        data={data || []}
        disableToolbar={!data}
        // setData={setData}
        skipPageReset={skipPageReset}
        tableTitle=""
        addData={addData}
        deleteRows={deleteRows}
        // tableOptions={tableOptions}
      />
    </div>
  );
};
export default CustomerListResults;
