import { Navigate } from 'react-router-dom';
import DashboardLayout from 'src/components/DashboardLayout';
import MainLayout from 'src/components/MainLayout';
import Account from 'src/pages/Account';
import MachinesList from 'src/pages/MachinesList';
import OperationsList from 'src/pages/OperationsList';
import StylesList from 'src/pages/StylesList';
import CategoriesList from 'src/pages/CategoriesList';
import Calculation from 'src/pages/Calculation';
import SAM from 'src/pages/SAMList';
import Login from 'src/pages/Login';
import NotFound from 'src/pages/NotFound';
import ProductList from 'src/pages/ProductList';
import Register from 'src/pages/Register';
import Settings from 'src/pages/Settings';

const routes = [
  {
    path: 'app',
    element: <DashboardLayout />,
    children: [
      { path: 'account', element: <Account /> },
      { path: 'machines', element: <MachinesList /> },
      { path: 'categories', element: <CategoriesList /> },
      { path: 'operations', element: <OperationsList /> },
      { path: 'styles', element: <StylesList /> },
      { path: 'dashboard', element: <Calculation /> },
      { path: 'products', element: <ProductList /> },
      { path: 'settings', element: <Settings /> },
      { path: 'sam', element: <SAM /> },
      { path: '*', element: <Navigate to="/404" /> }
    ]
  },
  {
    path: '/',
    element: <MainLayout />,
    children: [
      { path: 'login', element: <Login /> },
      { path: 'register', element: <Register /> },
      { path: '404', element: <NotFound /> },
      { path: '/', element: <Navigate to="/app/dashboard" /> },
      { path: '*', element: <Navigate to="/404" /> }
    ]
  }
];

export default routes;
