import React from 'react';

import CssBaseline from '@material-ui/core/CssBaseline';
import EnhancedTable from 'src/components/customTable/EnhancedTable';
import Backdrop from 'src/components/Backdrop';

import { useRequest } from 'src/utils/Fetch';

const CustomerListResults = () => {
  const columns = React.useMemo(
    () => [
      {
        Header: 'Name',
        accessor: 'Category_Name',
        id: 'Category_Name',
        columnName: 'Name',
        type: 'text',
        editable: true
      }
    ],
    []
  );

  const [data, setData] = React.useState([]);
  const [refresh, setRefresh] = React.useState(0);
  const [skipPageReset] = React.useState(false);
  const {
    get, post, put, response, loading, del
  } = useRequest();

  async function initializeData(key) {
    const initialTodos = await get(`/api/Categories?key=${key}`);
    if (response.ok) {
      setData(initialTodos);
    }
  }

  async function addData(body, type) {
    if (type === 'Add') {
      await post('/api/Categories', [body]);
    } else {
      await put('/api/Categories', [body]);
    }
    if (response.ok) {
      setRefresh((o) => o + 1);
    }
  }

  async function deleteRows(ids) {
    await del('/api/Categories', Object.keys(ids));
    if (response.ok) {
      setRefresh((o) => o + 1);
    }
  }

  React.useEffect(() => {
    initializeData(refresh);
  }, [refresh]);

  if (loading) {
    return <Backdrop open />;
  }
  return (
    <div>
      <CssBaseline />
      <EnhancedTable
        columns={columns}
        data={data}
        skipPageReset={skipPageReset}
        tableTitle="Categories"
        addData={addData}
        deleteRows={deleteRows}
      />
    </div>
  );
};
export default CustomerListResults;
