import React, { useState } from 'react';

// import AddIcon from '@material-ui/icons/Add';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
// import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
// import IconButton from '@material-ui/core/IconButton';
import PropTypes from 'prop-types';
// import Switch from '@material-ui/core/Switch';
import TextField from '@material-ui/core/TextField';

import DropdownCombobox from 'src/components/DropdownCombobox';
// import Tooltip from '@material-ui/core/Tooltip';

const AddUserDialog = ({
  initialValue, columns: cols = [], open, setOpen, options = {}, ...props
}) => {
  const columns = React.useMemo(() => cols.filter((c) => c.editable), [cols]);
  const initialUser = React.useMemo(() => columns.reduce((a, cv) => {
    const f = a;
    const y = initialValue?.[cv.id];
    if (cv.type === 'lookup') {
      f[cv.id] = y ? {
        label: y[cv.displayKey],
        key: y.Id
      } : null;
    } else {
      f[cv.id] = y;
    }
    return f;
  }, { Id: initialValue?.Id }), [columns]);
  const [user, setUser] = useState(initialUser);
  const { addUserHandler } = props;

  const [switchState, setSwitchState] = React.useState({
    addMultiple: false,
  });

  // const handleSwitchChange = (name) => (event) => {
  //   setSwitchState({ ...switchState, [name]: event.target.checked });
  // };

  const resetSwitch = () => {
    setSwitchState({ addMultiple: false });
  };

  const handleClose = () => {
    setOpen(null);
    resetSwitch();
  };

  const handleAdd = () => {
    addUserHandler(user, open);
    setUser(initialUser);
    setOpen(switchState.addMultiple ? open : null);
  };

  const handleChange = (name) => ({ target: { value } }) => {
    setUser({ ...user, [name]: value });
  };
  const handleChangeDropdown = (name) => (e, value) => {
    setUser({ ...user, [name]: value });
  };
  return (
    <div>

      <Dialog
        open={!!open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">{open}</DialogTitle>
        <DialogContent>
          {columns.map((c) => {
            if (c.type === 'lookup') {
              return (
                <DropdownCombobox
                  value={(user[c.id])?.label}
                  onChange={handleChangeDropdown(c.id)}
                  options={options[c.id] || []}
                  label={c.columnName}
                  width="100%"
                  margin="dense"
                  key={c.id}
                  sx={
                    {
                      mb: 2
                    }
                  }
                />

              );
            }
            return (
              <TextField
                autoFocus
                margin="dense"
                label={c.columnName}
                type="text"
                fullWidth
                value={user[c.id]}
                onChange={handleChange(c.id)}
                key={c.id}
              />
            );
          })}
        </DialogContent>
        <DialogActions>
          {/* <Tooltip title="Add multiple">
            <Switch
              checked={switchState.addMultiple}
              onChange={handleSwitchChange('addMultiple')}
              value="addMultiple"
              inputProps={{ 'aria-label': 'secondary checkbox' }}
            />
          </Tooltip> */}
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleAdd} color="primary">
            Submit
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

AddUserDialog.propTypes = {
  addUserHandler: PropTypes.func.isRequired,
  columns: PropTypes.shape({
    map: PropTypes.func
  }),
  initialUser: PropTypes.any,
  initialValue: PropTypes.func,
  open: PropTypes.any,
  options: PropTypes.object,
  setOpen: PropTypes.func
};

export default AddUserDialog;
