import PropTypes from 'prop-types';
import React, {
  useCallback, useReducer, useMemo, useContext
} from 'react';

import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';

const Context = React.createContext();

const initialState = {
  open: false,
  message: '',
};

const reducer = (state, action) => {
  switch (action.type) {
    case 'SHOW_SNACKBAR': {
      return {
        ...state,
        message: action.message,
        open: true,
      };
    }
    case 'CLOSE_SNACKBAR': {
      return {
        ...state,
        message: '',
        open: false,
      };
    }
    default:
      return state;
  }
};

export const SnackbarProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, initialState);

  const showSnackbar = useCallback(
    (message) => dispatch({ type: 'SHOW_SNACKBAR', message }),
    [dispatch]
  );
  const hideSnackbar = useCallback(
    () => dispatch({ type: 'CLOSE_SNACKBAR' }),
    [dispatch]
  );
  const value = useMemo(() => ({
    open: state.open, message: state.message, showSnackbar, hideSnackbar
  }), [state]);
  return (
    <Context.Provider value={value}>
      <SimpleSnackbar />
      {children}
    </Context.Provider>
  );
};

SnackbarProvider.propTypes = {
  children: PropTypes.any
};
export const useSnackbar = () => useContext(Context);
export function SimpleSnackbar() {
  const { open, hideSnackbar, message } = useSnackbar();

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    hideSnackbar();
  };

  return (
    <div>
      <Snackbar
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        open={open}
        autoHideDuration={6000}
        onClose={handleClose}
        message={message}
        action={(
          <>
            <IconButton size="small" aria-label="close" color="inherit" onClick={handleClose}>
              <CloseIcon fontSize="small" />
            </IconButton>
          </>
        )}
      />
    </div>
  );
}
