import React from 'react';

import Checkbox from '@material-ui/core/Checkbox';
import MaUTable from '@material-ui/core/Table';
import PropTypes from 'prop-types';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import Card from '@material-ui/core/Card';
// import Box from '@material-ui/core/Box';
// import TableFooter from '@material-ui/core/TableFooter';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
// import { green } from '@material-ui/core/colors';

import { makeStyles } from '@material-ui/core/styles';
// import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  useGlobalFilter,
  usePagination,
  useRowSelect,
  useSortBy,
  useTable,
} from 'react-table';
import TableToolbar from './TableToolbar';
// import TablePaginationActions from './TablePaginationActions';

const useStyles = makeStyles({
  root: {
    width: '100%',
    overflow: 'hidden',
  },
  container: {
    // maxHeight: 'calc(100vh - 250)',
  },
});

const IndeterminateCheckbox = React.forwardRef(
  ({ indeterminate, ...rest }, ref) => {
    const defaultRef = React.useRef();
    const resolvedRef = ref || defaultRef;

    React.useEffect(() => {
      resolvedRef.current.indeterminate = indeterminate;
    }, [resolvedRef, indeterminate]);

    return (
      <>
        <Checkbox
          ref={resolvedRef}
          color="default"
          // sx={{
          //   color: green[800],
          //   '&.Mui-checked': {
          //     color: green[600],
          //   },
          // }}
          {...rest}
        />
      </>
    );
  }

);
IndeterminateCheckbox.propTypes = {
  indeterminate: PropTypes.any
};

const inputStyle = {
  padding: 0,
  margin: 0,
  border: 0,
  background: 'transparent',
};

// Create an editable cell renderer
const EditableCell = ({
  value: initialValue,
  row: { index },
  column: { id },
  updateMyData, // This is a custom function that we supplied to our table instance
}) => {
  // We need to keep and update the state of the cell normally
  const [value, setValue] = React.useState(initialValue);

  const onChange = (e) => {
    setValue(e.target.value);
  };

  // We'll only update the external data when the input is blurred
  const onBlur = () => {
    updateMyData(index, id, value);
  };

  // If the initialValue is changed externall, sync it up with our state
  React.useEffect(() => {
    setValue(initialValue);
  }, [initialValue]);

  return (
    <input
      style={inputStyle}
      value={value}
      onChange={onChange}
      onBlur={onBlur}
    />
  );
};

EditableCell.propTypes = {
  cell: PropTypes.shape({
    value: PropTypes.any.isRequired
  }),
  column: PropTypes.shape({
    id: PropTypes.number.isRequired
  }),
  row: PropTypes.shape({
    index: PropTypes.number.isRequired
  }),
  updateMyData: PropTypes.func.isRequired,
  value: PropTypes.any
};
const HeaderComp = ({ getToggleAllRowsSelectedProps }) => (
  <div>
    <IndeterminateCheckbox {...getToggleAllRowsSelectedProps()} />
  </div>
);

HeaderComp.propTypes = {
  getToggleAllRowsSelectedProps: PropTypes.func
};

const CellComp = ({ row }) => (
  <div>
    <IndeterminateCheckbox {...row.getToggleRowSelectedProps()} />
  </div>
);

CellComp.propTypes = {
  row: PropTypes.shape({
    getToggleRowSelectedProps: PropTypes.func
  })
};
const defaultCell = ({
  value,
  column
}) => (column.type === 'lookup' ? value[column.displayKey] : value);
// Set our editable cell renderer as the default Cell renderer
const defaultColumn = {
  Cell: defaultCell,
};

const EnhancedTable = ({
  columns,
  data,
  // setData,
  addData,
  deleteRows,
  updateMyData,
  skipPageReset,
  tableTitle,
  tableOptions,
  disableToolbar,
}) => {
  const classes = useStyles();
  const getRowId = React.useCallback((row) => row.Id, []);
  const {
    getTableProps,
    headerGroups,
    prepareRow,
    page,
    gotoPage,
    setPageSize,
    // preGlobalFilteredRows,
    setGlobalFilter,
    selectedFlatRows,
    state: {
      pageIndex, pageSize, selectedRowIds, globalFilter
    },
  } = useTable(
    {
      columns,
      data,
      defaultColumn,
      autoResetPage: !skipPageReset,
      // updateMyData isn't part of the API, but
      // anything we put into these options will
      // automatically be available on the instance.
      // That way we can call this function from our
      // cell renderer!
      updateMyData,
      getRowId,
    },
    useGlobalFilter,
    useSortBy,
    usePagination,
    useRowSelect,
    (hooks) => {
      hooks.allColumns.push((_columns) => [
        // Let's make a column for selection
        {
          id: 'selection',
          // The header can use the table's getToggleAllRowsSelectedProps method
          // to render a checkbox.  Pagination is a problem since this will select all
          // rows even though not all rows are on the current page.  The solution should
          // be server side pagination.  For one, the clients should not download all
          // rows in most cases.  The client should only download data for the current page.
          // In that case, getToggleAllRowsSelectedProps works fine.
          Header: HeaderComp,
          // The cell can use the individual row's getToggleRowSelectedProps method
          // to the render a checkbox
          Cell: CellComp,
        },
        ..._columns,
      ]);
    }
  );

  const handleChangePage = (event, newPage) => {
    gotoPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setPageSize(Number(event.target.value));
  };

  // const removeByIndexs = (array, indexs) => array.filter((_, i) => !indexs.includes(i));

  const deleteUserHandler = () => {
    // const newData = removeByIndexs(
    //   data,
    //   Object.keys(selectedRowIds).map((x) => parseInt(x, 10))
    // );
    // setData(newData);
    deleteRows(selectedRowIds);
  };

  const addUserHandler = (user, open) => {
    // const newData = data.concat([user]);
    addData(user, open);
  };

  // Render the UI for your table
  return (
    <>
      {!disableToolbar && (
      <TableToolbar
        numSelected={Object.keys(selectedRowIds).length}
        deleteUserHandler={deleteUserHandler}
        addUserHandler={addUserHandler}
        // preGlobalFilteredRows={preGlobalFilteredRows}
        setGlobalFilter={setGlobalFilter}
        globalFilter={globalFilter}
        columns={columns}
        tableTitle={tableTitle}
        selectedFlatRows={selectedFlatRows}
        options={tableOptions}
      />
      )}
      <Card>
        <TableContainer style={{ maxHeight: 'calc(100vh - 360px)' }} className={classes.container}>
          <MaUTable stickyHeader size="small" {...getTableProps({ size: 'small' })}>
            <TableHead>
              {headerGroups.map((headerGroup) => (
                <TableRow {...headerGroup.getHeaderGroupProps()}>
                  {headerGroup.headers.map((column) => (
                    <TableCell
                      {...(column.id === 'selection'
                        ? column.getHeaderProps()
                        : column.getHeaderProps(column.getSortByToggleProps()))}
                    >
                      {column.render('Header')}
                      {column.id !== 'selection' ? (
                        <TableSortLabel
                          active={column.isSorted}
                      // react-table has a unsorted state which is not treated here
                          direction={column.isSortedDesc ? 'desc' : 'asc'}
                        />
                      ) : null}
                    </TableCell>
                  ))}
                </TableRow>
              ))}
            </TableHead>
            <TableBody>
              {page.map((row) => {
                prepareRow(row);
                return (
                  <TableRow {...row.getRowProps()}>
                    {row.cells.map((cell) => (
                      <TableCell {...cell.getCellProps()}>
                        {cell.render('Cell')}
                      </TableCell>
                    ))}
                  </TableRow>
                );
              })}
            </TableBody>

          </MaUTable>

        </TableContainer>

        {/* <TablePagination
                    rowsPerPageOptions={[
                      5,
                      10,
                      25,
                      { label: 'All', value: data.length },
                    ]}
                    colSpan={3}
                    count={data.length}
                    rowsPerPage={pageSize}
                    page={pageIndex}
                    SelectProps={{
                      inputProps: { 'aria-label': 'rows per page' },
                      native: true,
                    }}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                    ActionsComponent={TablePaginationActions}
                  /> */}
        <TablePagination
          rowsPerPageOptions={[10, 25, { label: 'All', value: data.length }]}
          component="div"
          count={data.length}
          rowsPerPage={pageSize}
          page={pageIndex}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />

      </Card>
    </>
  );
};

EnhancedTable.propTypes = {
  addData: PropTypes.func,
  columns: PropTypes.array.isRequired,
  data: PropTypes.array.isRequired,
  deleteRows: PropTypes.func, // setData: PropTypes.func.isRequired,
  disableToolbar: PropTypes.any,
  skipPageReset: PropTypes.bool.isRequired,
  tableOptions: PropTypes.any,
  tableTitle: PropTypes.any,
  updateMyData: PropTypes.func.isRequired
};

export default EnhancedTable;
