import React from 'react';

// import clsx from 'clsx';
// import DeleteIcon from '@material-ui/icons/Delete';
// import IconButton from '@material-ui/core/IconButton';
import { lighten, makeStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
// import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
// import Tooltip from '@material-ui/core/Tooltip';
import {
  Box,
  Button,
  Card,
  CardContent,
  TextField,
  InputAdornment,
  SvgIcon
} from '@material-ui/core';
import { Search as SearchIcon } from 'react-feather';
// import GlobalFilter from './GlobalFilter';
import AddUserDialog from './AddUserDialog';

const useToolbarStyles = makeStyles((theme) => ({
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1),
  },
  highlight:
    theme.palette.type === 'light'
      ? {
        color: theme.palette.secondary.main,
        backgroundColor: lighten(theme.palette.secondary.light, 0.85),
      }
      : {
        color: theme.palette.text.primary,
        backgroundColor: theme.palette.secondary.dark,
      },
  title: {
    flex: '1 1 100%',
  },
}));

export const CustomerListToolbar = ({
  setGlobalFilter, globalFilter, deleteUserHandler, numSelected, addUserHandler, columns, tableTitle, selectedFlatRows, options, ...props
}) => {
  const classes = useToolbarStyles();
  const [open, setOpen] = React.useState(null);
  return (
    <Box {...props}>
      {open && <AddUserDialog options={options} initialValue={open === 'Edit' ? selectedFlatRows?.[0]?.original : null} open={open} setOpen={setOpen} addUserHandler={addUserHandler} columns={columns} />}
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'flex-end'
        }}
      >
        <Typography className={classes.title} variant="h4" id="tableTitle">
          {tableTitle || 'data'}
        </Typography>
        {numSelected > 0 ? (
          <Typography
            className={classes.title}
            color="inherit"
            variant="subtitle1"
          >
            {numSelected}
            {' '}
            selected
          </Typography>
        ) : (
          ''
        )}
        <Button disabled={numSelected <= 0} onClick={deleteUserHandler}>
          Delete
        </Button>
        <Button disabled={numSelected !== 1} onClick={() => setOpen('Edit')}>
          Edit
        </Button>

        <Button
          color="primary"
          variant="contained"
          onClick={() => setOpen('Add')}
        >
          Add
        </Button>

      </Box>
      <Box sx={{ mt: 3 }}>
        <Card>
          <CardContent>
            <Box sx={{ maxWidth: 500 }}>
              <TextField
                fullWidth
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <SvgIcon
                        fontSize="small"
                        color="action"
                      >
                        <SearchIcon />
                      </SvgIcon>
                    </InputAdornment>
                  )
                }}
                placeholder="Search"
                variant="outlined"
                value={globalFilter || ''}
                onChange={(e) => {
                  setGlobalFilter(e.target.value || undefined); // Set undefined to remove the filter entirely
                }}
                size="small"
              />
            </Box>
          </CardContent>
        </Card>
      </Box>
    </Box>
  );
};

CustomerListToolbar.propTypes = {
  addUserHandler: PropTypes.any,
  columns: PropTypes.any,
  deleteUserHandler: PropTypes.any,
  globalFilter: PropTypes.string,
  numSelected: PropTypes.number,
  options: PropTypes.any,
  selectedFlatRows: PropTypes.any,
  setGlobalFilter: PropTypes.func,
  tableTitle: PropTypes.string
};

export default CustomerListToolbar;
