import React from 'react';

import CssBaseline from '@material-ui/core/CssBaseline';
import EnhancedTable from 'src/components/customTable/EnhancedTable';
import Backdrop from 'src/components/Backdrop';
import { useRequest } from 'src/utils/Fetch';
import DropdownCombobox from 'src/components/DropdownCombobox';

const CustomerListResults = () => {
  const columns = React.useMemo(
    () => [
      {
        Header: 'Name',
        accessor: 'Style_Name',
        id: 'Style_Name',
        columnName: 'Name',
        type: 'text',
        editable: true
      }
    ],
    []
  );

  const [data, setData] = React.useState(null);
  const [refresh, setRefresh] = React.useState(0);
  const [skipPageReset] = React.useState(false);
  const {
    get, post, response, loading, del, put
  } = useRequest();
  const [category, setCategory] = React.useState(null);
  const [categories, setCategories] = React.useState([]);

  async function initializeData(c, key) {
    const initialTodos = await get(
      `/api/styles?categoryId=${c.key}&key=${key}`
    );
    if (response.ok) {
      setData(initialTodos);
    }
  }

  async function addData(body, type) {
    if (type === 'Add') {
      await post(`/api/styles?categoryId=${category?.key}`, [body]);
    } else if (type === 'Edit') {
      await put(`/api/styles?categoryId=${category?.key}`, [body]);
    }
    if (response.ok) {
      setRefresh((o) => o + 1);
    }
  }

  async function deleteRows(ids) {
    await del(`/api/styles?categoryId=${category?.key}`, Object.keys(ids));
    if (response.ok) {
      setRefresh((o) => o + 1);
    }
  }

  React.useEffect(() => {
    if (category) {
      initializeData(category, refresh);
    }
  }, [refresh, category]);

  const handleChange = React.useCallback((event, o) => {
    event?.stopPropogation?.();
    setCategory(o);
  }, []);

  async function initializeCategories() {
    let items = await get('/api/Categories');
    items = items.map((o) => ({
      label: o.Category_Name,
      key: o.Id
    }));
    if (response.ok) {
      setCategories(items);
    }
  }

  React.useEffect(() => {
    initializeCategories();
  }, []);

  if (loading) {
    return <Backdrop open />;
  }

  return (
    <div>
      <CssBaseline />
      <DropdownCombobox
        onChange={handleChange}
        value={category?.label}
        options={categories}
        label="Category"
        size="small"
        sx={
          {
            mb: 2
          }
        }
      />
      <EnhancedTable
        columns={columns}
        data={data || []}
        disableToolbar={!data}
        skipPageReset={skipPageReset}
        tableTitle="Styles"
        addData={addData}
        deleteRows={deleteRows}
      />
    </div>
  );
};
export default CustomerListResults;
